import React, {Component} from 'react'
import './home.css';
import imgSrc from "./img/success@3x.png"

export default class Home extends Component {

  state = {
    tag: '',
    idea: '...',
    noticeShow: false
  }

  onIdeaTextarea = this.onIdeaTextarea.bind(this)

  onIdeaTextarea(e) {
    this.setState({
      idea: e.target.value
    })
  }

  noticeShow = this.noticeShow.bind(this)

  noticeShow() {
    this.setState({
      noticeShow: true
    })
  }

  noticeHide = this.noticeHide.bind(this)

  noticeHide() {
    this.setState({
      noticeShow: false
    })
  }


  render() {
    return (
      <div className="app">
        <div className="header">
          <p className="title_back">
            <i className="icon iconfont icon-back"></i>
            <span></span>
          </p>
          <h2 className="title"></h2>
        </div>
        <div className="main">
          <div className="mainWrap">
            <h4 className="tag">{this.state.tag}</h4>
            <div className="idea">
              <h2 className="mainTile"></h2>
              <textarea
                className="ideaText"
                onChange={this.onIdeaTextarea}
                value={this.state.idea}
                />
              <span className="limit">300</span>
            </div>
            <div className="upload">
              <h2 className="mainTile"></h2>
              <span className="uploadFile">
                <i className="icon iconfont icon-plus"></i>
                <input type="file" />
              </span>
            </div>
            <div className="submit">
              <button onClick={this.noticeShow}></button>
            </div>
          </div>
        </div>
        {
          this.state.noticeShow
          ?
          <div className="noticeWrap" onClick={this.noticeHide}>
            <div className="notice">
              <img src={imgSrc} alt="" />
              <div className="noticeMain">
                <div className="noticeContent">
                  <p className="noticeS"></p>
                  <p className="noticeL"><a href="/">-</a></p>
                </div>
              </div>
            </div>
          </div>
          : null
        }
      </div>
    )
  }
}



